
import Vue from 'vue'
import { useAuthStore } from '@/store/auth'
const authStore = useAuthStore();

export default Vue.extend({
  data: () => ({
    form: {
      username: '',
      password: ''
    },
    errors: []
  }), 
  methods: {
    submit() {
      authStore.login(this.form)
      .then(() => {
        this.$router.push('services')
      },(errors) => {
        this.errors = errors
      })
    }
  }
  
})
